/* Apply styles for devices with max-width 1366px and max-height 768px */
@media screen and (min-width: 768px){

 .contents {
  max-width: 614px !important;
 }
}

body {
  color:#565656;
  font-family: "Noto Sans KR", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  background-color: #D6D6D6;
}

html, body {
  max-width: 100%;
  overflow-x: hidden;
}

input {
  font-family: "Noto Sans KR", sans-serif;
  /* font-optical-sizing: auto; */
  font-weight: 400;
  
}

.font500 {
  font-weight: 500;
  font-family: "Noto Sans KR", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
}

.contents {
  text-align: center;
  background-color: #D6D6D6;
  color: #565656;
  max-width:767px;
  /* border: #fff solid 1px; */
  margin: 0 auto;
}

.bg-video {
  max-width: 100%;
  /* margin-top: 8em; */
  margin-bottom: 1em;
}
